@import '../../commons/scss/common.scss';
@import '../../commons/scss/breakpoint'; // 
@import 'base/variables'; // 
@import 'base/color'; // colors

@import 'base/util'; //mixins
@import 'mixins/layerfixed'; //mixins
@import 'mixins/underline'; //mixins
@import 'base/heading'; //
@import 'base/font-material';

/*  */
@import "components/sprite";
@mixin retina-sprites($retina-groups) {
	@each $retina-group in $retina-groups {
		$sprite-name: nth($retina-group, 1);
		.#{$sprite-name} {
			@include retina-sprite($retina-group);
		}	
	}
}
[class^='ico-'] {
	display: inline-block;
	vertical-align: middle;
}
@include retina-sprites($retina-groups);

@import 'base/layout'; //
@import 'base/gnb'; //
@import 'base/footer'; //

@import 'components/visible'; // 
@import 'components/popup'; //


a {
	color: $link-color;
}
html {
	font-size: $html-font-size;
	// @include breakpoint($sm){
	// 	font-size: rem(18);
	// }
}
body {
	overflow-x: hidden !important;
	color: $body-font-color;
	line-height: 1.5;
	font-family: $font-set;
	letter-spacing: -0.015rem;
	word-break: keep-all;
	font-weight: 400;
	@include breakpoint($sm) {
		word-break: keep-all;
	}
	&.ovh, 
	&.modal-open {
		//position: relative;
		overflow: hidden !important;
		//height: 100vh;
	}
}

* {
	box-sizing: border-box;
}

button {
	padding: 0;
	border: 0;
	background-color: transparent;
	overflow: visible;
}

i {
	font-style: normal;
}
img {
	max-width: 100%;
	// width: 100%;
}